body {
  background-color: #F8F7FC;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: .5px;
  font-size: 16px;
}

.percent-raised {
  color: #3cd483;
}

.percent-fallen {
  color: #d64d96;
}

.loading-container {
  width: 100%;
  text-align: center;
  margin: 40px auto;
}

.error {
  width: 100%;
  margin: 40px 0;
  text-align: center;
  color: #d64d96;
}

.Header {
    display: flex;
    flex-direction: row;
    align-items: center;
    /*B3B4CD */
    background-color: #B3B4CD ;
    width: 100%;
    height: 5vh;
    color: #FFF;
    font-size: 18px;
    padding-left: 10%;
    text-decoration: none;
  }
  


  
  .Table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 80%;
    margin-left:auto; 
    margin-right:auto;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 10px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
  }
  
  .Table-head {
    background-color: #FFF;
  }
  
  .Table-head tr th {
    color: #A4A9D7;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    height: 5vh;
  }
  
  .Table-body {
    text-align: left;
    background-color: #FFF;
  }
  
  .Table-body tr td{
    padding-top: 1.5%;
    padding-bottom: 1.5%;
    justify-content: center;
    text-align: center;
    border-bottom: 2px solid #FFF;
    color: #999;
    cursor: pointer;
  }
  
  .Table-rank {
    color: #999;
    font-size: 16px;
  }
  
  .Table-dollar {
    color: #999;
    font-size: 14px;
    justify-content: center;
  }

p{
  padding: none;
  margin: 0;
}
.Pagination {
    margin: 25 auto;
    text-align: center;
  }
  
  .Pagination-button {
    text-align: center;
    border: none;
    border-radius: 16px;
    background-color: #6B788F;
    -webkit-transition: background-color .2s;
    transition: background-color .2s;
    color: white;
    cursor: pointer;
    margin: 10px;
    width: 100px;
    height: 34px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
  }
  
  .Pagination-button:hover {
    background-color: #FFF;
    color: #B3B4CD;
  }
  
  .Pagination-button:focus {
    outline: none;
  }
  
  .Pagination-button:disabled {
    background-color: #B3B4CD;
    cursor: not-allowed;
  }
  
  .Pagination-info {
    font-size: 12px;
  }
.detail-container{
    width: 80%;
    justify-content: center;
    margin-left:auto; 
    margin-right:auto;
    margin: 0 auto;
    margin-top: 20px;
}

#chart-container {
    min-width: 80%;
    height: 100%;
    margin-top: 10px;
    justify-content: center;
    text-align: center;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);

  }

#news-container{
    width: 48.5%;
    min-height: 50%;
    height: 100%;
    background-color: white;
    margin-top: 10px;
    padding: 10px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
    color: #999;
    display: inline-block;
    margin-right: 5px;
}

.news-container-right{
    width: 48.5%;
    min-height: 50%;
    height: 100%;
    background-color: white;
    margin-top: 10px;
    padding: 10px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
    color: #999;
    display: inline-block;
    margin-left: 5px;
    float:right;

}
.newsItem{
    background-color: white;
    color: #999;
    cursor: pointer;
}
.newsItem:hover{
    background-color: #B3B4CD;
    color: white;
}

#newstext {
    font-size: 24px;
    margin-top: 0;
}
