.detail-container{
    width: 80%;
    justify-content: center;
    margin-left:auto; 
    margin-right:auto;
    margin: 0 auto;
    margin-top: 20px;
}

#chart-container {
    min-width: 80%;
    height: 100%;
    margin-top: 10px;
    justify-content: center;
    text-align: center;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);

  }

#news-container{
    width: 48.5%;
    min-height: 50%;
    height: 100%;
    background-color: white;
    margin-top: 10px;
    padding: 10px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
    color: #999;
    display: inline-block;
    margin-right: 5px;
}

.news-container-right{
    width: 48.5%;
    min-height: 50%;
    height: 100%;
    background-color: white;
    margin-top: 10px;
    padding: 10px;
    box-shadow: 0 0 12px 0 hsla(0, 0%, 0%, 0.2);
    color: #999;
    display: inline-block;
    margin-left: 5px;
    float:right;

}
.newsItem{
    background-color: white;
    color: #999;
    cursor: pointer;
}
.newsItem:hover{
    background-color: #B3B4CD;
    color: white;
}

#newstext {
    font-size: 24px;
    margin-top: 0;
}